html {
  --color-black: #0e111b;
  --color-white: #fff;
  --color-white-50: #ffffff80;
  --color-white-30: #ffffff4d;
  --color-white-20: #fff3;
  --color-white-10: #ffffff1a;
  --color-white-5: #ffffff0d;
  --color-primary: #fdc000;
  --color-hover: #f86a05;
  --color-area: #1c212e;
}

body {
  background-color: var(--color-black);
  color: var(--color-white);
  font-family: Roboto, sans-serif;
  line-height: 1.4;
}

.btn {
  color: var(--color-white);
  background-color: var(--color-area);
  cursor: pointer;
  border: none;
  border-radius: 4px;
  outline: none;
  height: 50px;
  padding: 0 20px;
  font-size: 14px;
  font-weight: 700;
  transition: all .3s;
}

.btn:hover {
  background-color: var(--color-hover);
}

.btn-primary {
  background-color: var(--color-primary);
  color: var(--color-black);
}

.btn-primary:hover {
  background-color: var(--color-hover);
  color: var(--color-white);
}

.the-loader {
  border: 4px solid var(--color-primary);
  border-top-color: #0000;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  margin: 30px auto;
  animation: 1s linear infinite loader;
}

.the-loader.hide {
  display: none;
}

@keyframes loader {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.skeleton {
  background-color: var(--color-area);
  border-radius: 10px;
  position: relative;
  overflow: hidden;
}

.skleton:after {
  content: "";
  background-image: linear-gradient(270deg, #fff0, #ffffff1a, #fff0);
  width: 100%;
  height: 100%;
  animation: 2s infinite skeleton-loader;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

@keyframes skeleton-loader {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(100%);
  }
}

.container {
  max-width: 1120px;
  margin: 0 auto;
  padding: 40px 0;
}

.headline {
  margin-bottom: 40px;
}

.headline h1 {
  margin-bottom: 40px;
  font-family: Oswald, sans-serif;
  font-size: 80px;
  line-height: 1;
}

.headline h1 span {
  color: var(--color-primary);
}

.headline p {
  color: var(--color-white-30);
}

.search {
  gap: 10px;
  margin-bottom: 30px;
  display: flex;
}

.search input {
  color: var(--color-white);
  background-color: var(--color-area);
  border: none;
  border-radius: 4px;
  outline: none;
  flex-grow: 1;
  height: 50px;
  padding: 0 20px;
  font-size: 14px;
}

.search input::placeholder {
  color: var(--color-white-30);
}

.search .btn {
  flex-grow: 1;
  max-width: 150px;
}

.movie-list {
  background-color: var(--color-area);
  border-radius: 4px;
  padding: 20px;
}

.movie-list .message {
  color: var(--color-primary);
  text-align: center;
  font-family: Oswald, sans-serif;
  font-size: 20px;
}

.movie-list .movies {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.movies .movie {
  --width: 200px;
  width: var(--width);
  height: calc(var(--width) * 3 / 2);
  background-color: var(--color-black);
  background-size: cover;
  border-radius: 4px;
  position: relative;
  overflow: hidden;
}

.movies .movie:hover:after {
  content: "";
  border: 6px solid var(--color-primary);
  position: absolute;
  inset: 0;
}

.movies .movie .info {
  box-sizing: border-box;
  text-align: center;
  backdrop-filter: blur(10px);
  background-color: #0e111b4d;
  width: 100%;
  padding: 14px;
  font-size: 14px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.movies .movie .info .year {
  color: var(--color-primary);
}

.movies .movie .info .title {
  color: var(--color-white);
}

.view-more {
  width: 100%;
  max-width: 300px;
  margin: 20px auto;
  display: block;
}

.view-more.hide {
  display: none;
}

.the-movie {
  color: var(--color-white-50);
  gap: 70px;
  display: flex;
}

.the-movie .poster {
  --width: 500px;
  width: var(--width);
  height: calc(var(--width) * 3 / 2);
  background-size: cover;
  background-color: var(--color-area);
  border-radius: 10px;
  flex-shrink: 0;
}

.the-movie .description {
  flex-grow: 1;
}

.description .title {
  color: var(--color-white);
  margin-bottom: 30px;
  font-family: Oswald, sans-serif;
  font-size: 70px;
  line-height: 1;
}

.description .title.skeleton {
  height: 70px;
}

.description .labels span {
  color: var(--color-primary);
  margin-bottom: 20px;
  font-family: Roboto, sans-serif;
}

.description .labels.skeleton {
  height: 30px;
}

.the-movie .plot.skeleton {
  width: 88%;
  height: 400px;
}

.the-movie .site {
  font-family: Oswald, sans-serif;
}

.site-icon {
  vertical-align: middle;
  color: #0000;
  background-position: center;
  background-size: cover;
  width: 17px;
  height: 17px;
  margin-right: 8px;
  display: inline-block;
}

.site-icon.Internet.Movie.Database {
  background-image: url("https://m.media-amazon.com/images/M/MV5BOTc2YzQ0NDctMzViZi00YjNkLWIxODktZDFjYTg5ZDI3YjhjXkEyXkFqcGdeQXVyMTEyMjM2NDc2._V1_.jpg");
}

.site-icon.Metacritic {
  background-image: url("https://play-lh.googleusercontent.com/Mp_32pK1zEZMjw1RZZsiEE_pjGKArdykWzi9H-82n7IKIXvbgSDSqWHHdhEPtiBVLA");
}

.site-icon.Rotten.Tomatoes {
  background-image: url("https://yt3.googleusercontent.com/ytc/AIdro_lu_2dewkGiITum97t_8qF9ZAzjRRYqxqWYLfaeMy_1Oh5Q=s900-c-k-c0x00ffffff-no-rj");
}

.the-movie h3 {
  color: var(--color-white);
  margin: 24px 0 6px;
  font-family: Oswald, sans-serif;
  font-size: 20px;
}
